import { useState } from "react";
import { Thumbs } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react"

type PorductSwiperType = {
  imagesBig: string[];
  imagesSmall: string[]
}

const ProductSwiper: React.FC<PorductSwiperType> = ({ imagesBig, imagesSmall }) => {
  const [thumbsSwiper, setThumbsSwiper] = useState<any>(null);
  return (
    <div className="main-product__images images-product">
      <Swiper
        modules={[Thumbs]} 
        thumbs={{ swiper: thumbsSwiper }}
        slidesPerView={1}
        speed={800}
        className="images-product__slider swiper">
        {imagesBig && imagesBig.map((item, i) => {
          return <SwiperSlide key={i} className="images-product__item swiper-slide">
            <div className="images-product__image">
              <img src={imagesBig[i]} alt="product" />
            </div>
          </SwiperSlide>
        })}
      </Swiper>
      <Swiper
        modules={[Thumbs]}
        watchSlidesProgress
        onSwiper={setThumbsSwiper}
        slidesPerView={4}
        direction="horizontal"
        spaceBetween={8}
        breakpoints={{
          600: {
            direction: "vertical",
          }
        }}
        className="images-product__subslider swiper">
        {imagesSmall && imagesSmall.map((item, i) => {
          return <SwiperSlide key={i} className='images-product__subitem swiper-slide'>
            <div className="images-product__subimage">
              <img src={imagesSmall[i]} alt="product" />
            </div>
          </SwiperSlide>
        })}
      </Swiper>
    </div>
  )
}
export default ProductSwiper