import clsx from "clsx"
import { useState } from "react"
import { FetchStatusType } from "../../common/types";
import { AllType} from "../../redux/product/types"

import InforProductSkeleton from "./InfoProductSkeleton";
type PropType = {
  all: AllType[];
  extra: AllType[];
  fetchStatus: string
}
const InfoProduct: React.FC<PropType> = ({ all, extra, fetchStatus }) => {
  const [activeTab, setActiveTab] = useState("1")
  const [activeBlock, setActiveBlock] = useState("1")
  const navOnClick = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    const eTarget = e.target as HTMLDivElement
    let getAtttr = eTarget.getAttribute("data-to") as string
    setActiveTab(getAtttr)
    setActiveBlock(getAtttr)
  }
  function setTabElement(nmb: string, text: string) {
    return <div onClick={(e) => navOnClick(e)} className={clsx("info-product__navitem", { "active": activeTab === nmb })} data-to={nmb}>{text}</div>
  }
  function setBlockElement(item: AllType) {
    return (
      <>
        <div className="block-info__title">{item.name}</div>
        <ul className="block-info__list">
          {item.properties.map((el, i) => <li key={i} className="block-info__item">{el.name}<span>{el.value}&nbsp;{el.measure}</span></li>)}
        </ul>
      </>

    )
  }
  return (
    <div className="product-page__info info-product">
      <div className="info-product__nav">
        {setTabElement("1", "Все характеристики")}
        {setTabElement("2", "Доп.информация")}
      </div>
      {fetchStatus === FetchStatusType.fulfilled ? (<div className="info-product__blocks">
        {activeBlock === "1" && <div className={clsx("info-product__block block-info", { "active": activeBlock === "1" })} data-to="1">
          <div className="block-info__cols">
            {all && all.map((item, i) => <div key={i} className="block-info__col">
              {setBlockElement(item)}
            </div>)}
          </div>
        </div>}
        {activeBlock === "2" && <div className={clsx("info-product__block block-info", { "active": activeBlock === "2" })} data-to="2">
          <div className="block-info__cols">
            {extra && extra.map((item, i) => <div key={i} className="block-info__col">
              {setBlockElement(item)}
            </div>)}
          </div>
          <div className="block-info__footer">* Учитываются самые свежие версии программ</div>
        </div>}
      </div>) : <InforProductSkeleton /> }
    </div>

  )
}
export default InfoProduct