import { useEffect } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { fetchProduct } from "../redux/product/actions"
import { useAppDispatch, useAppSelector } from "../redux/store"
import ProductSwiper from "../components/Product/ProductSwiper"
import InfoProduct from "../components/Product/InfoProduct"
import ProductSkeleton from "../components/Product/ProductSkeleton"
import TitleSkeleton from "../components/Product/TitleSkeleton"
import { FetchStatusType } from "../common/types"

const ProductPage:React.FC = () => {
  const { item, fetchStatus } = useAppSelector(state => state.product)
  const { name, imagesBig, imagesSmall, properties } = item
  const navigate = useNavigate()
  const params = useParams()
  const dispatch = useAppDispatch()
  useEffect(() => {
    let paramsId = params.id
    if (paramsId) dispatch(fetchProduct(paramsId))
    // eslint-disable-next-line react-hooks/exhaustive-deps 
  }, [])
  useEffect(() => {
    if (fetchStatus === FetchStatusType.rejected) {
      navigate("/notfound") 
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps 
  }, [fetchStatus])
  return (
    <div className="page__product product-page">
      <div className="product-page__container container">
      <div className="product-page__inner">
      <div className="product-page__main main-product">
        <div className="main-product__header">
        {fetchStatus === FetchStatusType.fulfilled ? <div className="main-product__title">{name}</div> : <TitleSkeleton />}
        </div>
        {fetchStatus === FetchStatusType.fulfilled ? <ProductSwiper imagesBig={imagesBig} imagesSmall={imagesSmall}/> : <ProductSkeleton /> }       
        <div className="main-product__details details-product">
          <div className="details-product__title">Основные характеристики ноутбука</div>
          <div className="details-product__body">
            {properties.keys && properties.keys.map((item,i) => <div key={i} className="details-product__item"><span>{item.name}</span>{item.value}</div>)}
          </div>
        </div>
      </div>
      <div className="product-page__marketplace marketplace">
        <ul className="marketplace__list">
          <li className="marketplace__item"><a href="#"><span>М.Видео</span><span>69 990 ₽</span></a></li>
          <li className="marketplace__item"><a href="#"><span>Эльдорадо</span><span>69 990 ₽</span></a></li>
          <li className="marketplace__item"><a href="#"><span>ДНС</span><span>69 990 ₽</span></a></li>
          <li className="marketplace__item"><a href="#"><span>Ситилинк</span><span>69 990 ₽</span></a></li>
        </ul>
      </div>
      <InfoProduct fetchStatus={fetchStatus} all={properties.all} extra={properties.extra}/>
    </div>
      </div>
    </div>
  )
}
export default ProductPage