import React from "react"
import ContentLoader from "react-content-loader"

const InforProductSkeleton = () => (
  <ContentLoader  viewBox="0 0 100 670" height={670} width={'100%'}
    speed={2}
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
    preserveAspectRatio="none"
  >
    <rect x="0" y="20" rx="0" ry="0" width="47%" height="200" /> 
    <rect x="53%" y="20" rx="0" ry="0" width="47%" height="200" /> 
    <rect x="0" y="240" rx="0" ry="0" width="47%" height="200" />
    <rect x="53%" y="240" rx="0" ry="0" width="47%" height="200" /> 
    <rect x="0" y="460" rx="0" ry="0" width="47%" height="200" /> 
    <rect x="53%" y="460" rx="0" ry="0" width="47%" height="200" />  
  </ContentLoader>

)

export default  InforProductSkeleton


